import { _decorator, Component, Enum, Node, EventTarget } from "cc";
import { UIManager } from "./UIManager";

const { ccclass, property } = _decorator;

export enum GameState {
  LOADING,
  INIT,
  GAME_START,
  GAME_RUNNING,
  GAME_OVER,
}
export const gameEventTarget = new EventTarget();

@ccclass("GameManager")
export class GameManager extends Component {
  @property({ type: Enum(GameState) })
  currentState: GameState = GameState.LOADING;

  protected onLoad(): void {
    this.setState(GameState.LOADING);
  }

  protected start(): void {
    this.scheduleOnce(() => {
      this.setState(GameState.INIT);
    }, 3);
  }

  setState(newState: GameState) {
    this.currentState = newState;
    this.handleStateChange(newState);
  }

  handleStateChange(state: GameState) {
    switch (state) {
      case GameState.LOADING:
        gameEventTarget.emit("loading");
        break;
      case GameState.INIT:
        gameEventTarget.emit("init");
        break;
      case GameState.GAME_START:
        gameEventTarget.emit("game_start");
        break;
      case GameState.GAME_RUNNING:
        gameEventTarget.emit("game_running");
        break;
      case GameState.GAME_OVER:
        gameEventTarget.emit("game_over");
        break;
    }
  }
}
